import React from "react";
import Header from "./Header";

class Layout extends React.Component {
  renderContent = props => {
    return (
      <div className="col-xs-12 hi">
        <div className="row firs-content-row">
          <div className="col-xs-4 col-xs-offset-2 first-content-row">
            <h2>Stap in, leg richtlijn vast en bereik je doel!</h2>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
              enim ad minim veniam, quis nostrud exercitation ullamco laboris
              nisi
            </p>
          </div>
          <div className="col-xs-4 second-content-row" />
        </div>
        <div className="row second-content-row">
          <div className="col-xs-4 col-xs-offset-2">
            <h2>Je bepaalt zelf de omvang</h2>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
              eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut
              enim ad minim veniam, quis nostrud exercitation ullamco laboris
              nisi
            </p>
          </div>
          <div className="col-xs-4 col-xs-offset-2" />
        </div>
      </div>
    );
  };

  render() {
    return (
      <div className="container">
        <Header tagline="Log in" />
        {this.props.children === undefined
          ? this.renderContent()
          : this.props.children}
      </div>
    );
  }
}

export default Layout;



